
import axios from '../actions/ajax';

import { host } from '../utils';

import connect from '@vkontakte/vkui-connect';


const SELECTGROUP = 'SELECTGROUP';
const FOLLOWERS = 'FOLLOWERS';
const FOLLOWERS_LOADING = 'FOLLOWERS_LOADING';
const FOLLOWERS_COUNT = 'FOLLOWERS_COUNT';
const MYFOLLOWER = 'MYFOLLOWER';
const USER = 'USER';

const initialState = {
    user: {},
    myAccount: null,
    group: {},
    followers: [],
    followers_count: [],
    followers_loading: false
};

export const storeRatePage = {
    regVKApp: () => async (dispatch, getState) => {
        connect.subscribe((e) => {
            switch (e.detail.type) {
                case 'VKWebAppGetUserInfoResult':
					dispatch({ type: USER, payload: e.detail.data });
                    break;                
				case 'VKWebAppCommunityAccessTokenReceived':
					console.log("VKWebAppCommunityAccessTokenReceived",e)
                    break;
                default:
                    console.log(e.detail.type);
            }
        });
    },
    vkWebAppGetUserInfo: () => async (dispatch, getState) => {
        connect.send('VKWebAppGetUserInfo', {});
    },
	showSettingsBox: () => async (dispatch, getState) => {
		//connect.send('VKWebAppGetUserInfo', {});
		console.log("VKWebAppGetCommunityAuthToken")
        connect.send("VKWebAppGetCommunityAuthToken", {"app_id": 7104226, "group_id": 152894915, "scope": "messages,manage,photos,docs,wall,stories,app_widget"});
    },
    getFollowers: (groupId,sorter) => async (dispatch, getState) => {
        if (!(groupId != null && groupId > 0)) {
            dispatch({ type: FOLLOWERS_COUNT, payload: 0 });
            dispatch({ type: FOLLOWERS, payload: [] });
            return;
        }
        dispatch({ type: FOLLOWERS_LOADING, payload: true });
        axios.get(host + "/followers/GetFollowersFilter", {
            params: {
                groupId,
                sorter
            }
        })
        .then((result) => {
            result = result.data;
            if (result.isSuccess) {
                dispatch({ type: FOLLOWERS_COUNT, payload: result.object.count });
                dispatch({ type: FOLLOWERS, payload: result.object.items });
                dispatch({ type: FOLLOWERS_LOADING, payload: false });
            }
        });
        
    }, getFollowerById: (groupId, followerId) => async (dispatch, getState) => {
        if (!followerId) return;
        axios.get(host + "/followers/GetFollowersFilter", {
            params: {
                groupId,
                followerId
            }
        })
            .then((result) => {
                result = result.data;
                if (result.isSuccess) {
                    if (result.object.count > 0) {
                        dispatch({ type: MYFOLLOWER, payload: result.object.items[0] });
                    }
                } else {
                    dispatch({ type: MYFOLLOWER, payload: null});
                }
            });

    },
    getGroupById: (socialId) => async (dispatch, getState) => {

        axios.get(host + "/group/GetGroupById", {
            params: {
                socialId
            }
        })
            .then((result) => {
                result = result.data;
                if (result.isSuccess) {
                    dispatch({ type: SELECTGROUP, payload: result.object });
                } else {
                    dispatch({ type: SELECTGROUP, payload: null });
                }

            }).catch((e) => {
                console.log(e);
                dispatch({ type: SELECTGROUP, payload: null });
            });

    }
    
};

export const reducer = (state = initialState, action) => {
    switch (action.type) {
        case SELECTGROUP: return { ...state, group: action.payload};
        case FOLLOWERS: return { ...state, followers: [ ...action.payload]};
        case FOLLOWERS_COUNT: return { ...state, followers_count: action.payload};
        case FOLLOWERS_LOADING: return { ...state, followers_loading: action.payload};
        case MYFOLLOWER: return { ...state, myAccount: action.payload};
        case USER: return { ...state, user: action.payload};
        default:
            return state;
    }
};
