import React, { Component } from 'react';

import {bindActionCreators} from  'redux';
import {connect} from 'react-redux';

import { getParamsUrl } from '../utils';

import "./ShopPage.less";
import { storeRatePage } from "../store/RatePage";
import { NavBar, WhiteSpace, Grid } from 'antd-mobile';
import { Col, Icon, Avatar,Card, List,Row, Button } from 'antd';

const { Meta } = Card;

const { getFollowers, getGroupById, getFollowerById, regVKApp, vkWebAppGetUserInfo } = storeRatePage;

class ShopPage extends Component{
    componentDidMount() {

    }
    render() {
        const { myAccount, tovars } = this.props;
        return (
            <Row style={{ background: "linear-gradient(45deg, red, blue)" }} className="shop">
                <NavBar
                    mode="dark"
                    leftContent={[
                        <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                    ]}
                    rightContent={[
                        <div>
                            <img alt="" src="./images/coins.png" />
                            {myAccount ? myAccount.balls : 0}
                        </div>
                    ]}
                >
                    
                    Магазин
                </NavBar>
                <Row style={{ margin: "40px", marginTop: "0px", background: "linear-gradient(45deg, red, blue)"}} gutter={32}>
                    {
                        <List
                            grid={{
                                gutter: 16,
                                xs: 1,
                                sm: 2,
                                md: 2,
                                lg: 3,
                                xl: 3,
                                xxl: 3,
                            }}
                            pagination={{
                                pageSize: 6,
                                total: 24,
                            }}
                            dataSource={tovars}
                            renderItem={item => (
                                <List.Item key={item.tovarId}>
                                    <Card
                                        bordered={false}
                                        hoverable
                                        actions={[
                                            <Button type="link">
                                                Купить
                                            </Button>
                                        ]}
                                        style={{ marginTop: "20px" }}
                                        cover={<img alt="example" src={item.photo} />}
                                    >
                                        <Meta title={item.name} description={item.description} />
                                        <span className="price" style={{ paddingTop: "10px", float: "left", textAlign: "center", width: "100%" }}>{item.price} руб.</span>
                                    </Card>
                                </List.Item>
                            )}
                        />
                    }
                </Row>
                
            </Row>
            
        );
	}
};
function mapStateToProps (state) {
    return {
        tovars: [1, 2, 3, 4, 5, 6, 7, 4214, 21, 421, 4, 2, 32, 32, 3, 24231, 32, 13, 2, 13, 21, 32, 13, 2].map((s,i) => ({
            name: "Название " + s,
            description: "Описание",
            price: s * 60,
            photo: "https://www.argnord.ru/pictures/actions/mega-sale-letnyaya-kollektsiya.jpg",
            tovarId:i
        }))
    };
}
function matchDispatchToProps(dispatch){ 
    return bindActionCreators({ },dispatch);
}
export default connect(mapStateToProps,matchDispatchToProps)(ShopPage);
