import { message } from 'antd';
import axios from '../actions/ajax';

import { host } from '../utils';


const USERPAGE_USER = 'USERPAGE_USER';
const USERPAGE_USER_LOADING = 'USERPAGE_USER_LOADING';

const USERPAGE_GROUPS = 'USERPAGE_GROUPS';
const USERPAGE_GROUPS_LOADING = 'USERPAGE_GROUPS_LOADING';

const USERPAGE_ACTION_UPDATE = 'USERPAGE_ACTION_UPDATE';
const USERPAGE_ACTIONS_COUNT = 'USERPAGE_COUNT';
const USERPAGE_ACTIONS = 'USERPAGE_ACTIONS';
const USERPAGE_ACTIONS_LOADING = 'USERPAGE_ACTIONS_LOADING';

const USERPAGE_USER_STATISTIC = 'USERPAGE_USER_STATISTIC';

const initialState = {
    user: {},
    user_loading: false,

    groups:[],
    groups_loading:false,

    actions: [],
    actions_count: 0,
    actions_loading: false,

    statistic: [],
};
export const storeUserPage = {
    getUser: (followerId) => async (dispatch, getState) => {
        dispatch({ type: USERPAGE_USER_LOADING, payload: true });
        axios.get(host + "/followers/GetFollowersFilter", {
            params: {
                followerId
            }
        })
        .then((result) => {
            result = result.data;
            dispatch({ type: USERPAGE_USER_LOADING, payload: false });
            if (result.isSuccess) {
                dispatch({ type: USERPAGE_USER, payload: result.object.items[0] });
            } else {
                message.error("��� �� ����� �� ���. ������������� ��������");
                dispatch({ type: USERPAGE_USER, payload:[]});
            }
        })
        .catch(() => {
            message.error("��� �� ����� �� ���. ������������� ��������");
            dispatch({ type: USERPAGE_USER_LOADING, payload: false });
            dispatch({ type: USERPAGE_USER, payload: [] });
        });
    },
    getGroupsByFollower: (followerId) => async (dispatch, getState) => {
        dispatch({ type: USERPAGE_GROUPS_LOADING, payload: true });
        axios.get(host + "/group/GetGroupsFilterV2", {
            params: {
                followerId
            }
        })
        .then((result) => {
            result = result.data;
            dispatch({ type: USERPAGE_GROUPS_LOADING, payload: false });
            if (result.isSuccess) {
                dispatch({ type: USERPAGE_GROUPS, payload: result.object});
            }
        })
        .catch(() => {
            message.error("��� �� ����� �� ���. ������������� ��������");
            dispatch({ type: USERPAGE_GROUPS_LOADING, payload: false });
            dispatch({ type: USERPAGE_GROUPS, payload: [] });
        });
    },
    changeBalls: (followersGroupActionId, isAddBalls) => async (dispatch, getState) => {
        dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: true });
        axios.post(host + "/followers/ChangeBalls", {
            followersGroupActionId,
            isAddBalls
        })
            .then((result) => {
                result = result.data;
                dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: false });
                if (result.isSuccess) {
                    dispatch({ type: USERPAGE_ACTION_UPDATE, payload: { followersGroupActionId, isAddBalls } });
                }
            })
            .catch(() => {
                message.error("��� �� ����� �� ���. ������������� ��������");
                dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: false });
            });
    },
    getActionsStatistic: (followerId, groupId) => async (dispatch, getState) => {
        axios.get(host + "/followers/getActionsStatistic", {
            params: {
                followerId,
                groupId,
            }
        })
            .then((result) => {
                result = result.data;
                if (result.isSuccess) {
                    dispatch({ type: USERPAGE_USER_STATISTIC, payload: result.object });
                }
            })
            .catch(() => {
                message.error("��� �� ����� �� ���. ������������� ��������");
            });
    },
    changeBallsFollower: (followerId, groupId) => async (dispatch, getState) => {
        axios.post(host + "/followers/ChangeBallsByUser", {
                followerId,
                groupId,
        })
            .then((result) => {
                result = result.data;
                if (result.isSuccess) {
                    dispatch({ type: USERPAGE_ACTIONS, payload: [] });
                    dispatch({ type: USERPAGE_USER_STATISTIC, payload: { likes: 0, comments: 0, reposts: 0 } });
                }
            })
            .catch(() => {
                message.error("��� �� ����� �� ���. ������������� ��������");
            });
    },
    getFollowerActionsFilter: (followerId, groupId,sorter) => async (dispatch, getState) => {
        dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: true });
        axios.get(host + "/followers/GetFollowerActionsFilter", {
            params: {
                followerId,
                groupId,
                sorter
            }
        })
        .then((result) => {
            result = result.data;
            dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: false });
            if (result.isSuccess) {
                dispatch({ type: USERPAGE_ACTIONS_COUNT, payload: result.object.count });
                dispatch({ type: USERPAGE_ACTIONS, payload: result.object.items });
            }
        })
        .catch(() => {
            message.error("��� �� ����� �� ���. ������������� ��������");
            dispatch({ type: USERPAGE_ACTIONS_LOADING, payload: false });
            dispatch({ type: USERPAGE_ACTIONS_COUNT, payload: 0 });
            dispatch({ type: USERPAGE_ACTIONS, payload: [] });
        });
    }
};

export const reducer = (state = initialState, action) => {
    switch (action.type) {
        case USERPAGE_USER: return { ...state, user: {...action.payload}};
        case USERPAGE_USER_LOADING: return { ...state, user_loading: action.payload };

        case USERPAGE_GROUPS: return { ...state, groups: [...action.payload] };
        case USERPAGE_GROUPS_LOADING: return { ...state, groups_loading: action.payload };

        case USERPAGE_ACTIONS: return { ...state, actions: [...action.payload] };
        case USERPAGE_ACTION_UPDATE: {
            return {
                ...state, actions: state.actions.map(act =>
                    act.followersGroupActionId === action.payload.followersGroupActionId ?
                        { ...act, isAddBalls: action.payload.isAddBalls }
                        :
                        act
                )
            };
        }
        case USERPAGE_ACTIONS_COUNT: return { ...state, actions_count: action.payload};
        case USERPAGE_ACTIONS_LOADING: return { ...state, actions_loading: action.payload };

        case USERPAGE_USER_STATISTIC: return { ...state, statistic: action.payload};
        default:
            return state;
    }
};
